import { Divider } from "@mui/material";
import Features from "../components/mui/Features";
import VerifiedUserIcon from '@mui/icons-material/VerifiedUser';
import FAQ from "../components/mui/FAQ";
import Footer from "../components/mui/Footer";
import SaveIcon from '@mui/icons-material/Save';
import ClearAllIcon from '@mui/icons-material/ClearAll';
import SlowMotionVideoIcon from '@mui/icons-material/SlowMotionVideo';

export function Dashboard() {
    return <div className="container mx-auto">
        <div className="mt-20"></div>
        <Features nohero title="Features"
            description="Spotify offers many audio books but playing them and returning to the current played title is not well implemented. This app tracks the played playlists (your audio books) and lets you return to the last played track of the audio book."
            items={[
                {
                    title: "Save your audiobooks",
                    description: "The app registers songs played by Spotify. When you play a song that is in a playlist that is not yet registered in the app as an audiobook, the playlist will be registered and the current track saved.",
                    icon: <SaveIcon />,
                    component: < SaveAudioBook />,
                }, {
                    title: "Return to the last played track",
                    description: "If you switch the audio book or want to play your favourit tunes instead of the current book, returning to the last track from that book is cumbersome. The App lists all the played playlists and lets you continue where you left off.",
                    icon: <SlowMotionVideoIcon />,
                    component: < ContinueAudioBook />,
                }, {
                    title: "Manage your audio books",
                    description: "Once a playlist is registered in the app you can either delete or verify it as an audio book.",
                    icon: <ClearAllIcon />,
                    component: < ManageAudioBook />,
                }
            ]} />

        <Divider />
        <FAQ title="Frequently asked questions" content={[{ id: '1', q: 'Do i have to login to the application?', a: 'No. You only have to listen to tracks on your device.' },
        { id: '2', q: 'How does it work?', a: "The application listens on your local device for any tracks you are playing. If a track of an unregistered playlist is played, the playlist is registered as a possible audio book." }]} />
        <Divider />
        <Footer company="JustMePrd"
            companylink="https://Justmeprd.com" />
    </div>
}

function SaveAudioBook() {
    return <SaveIcon />
}

function ContinueAudioBook() {
    return <SlowMotionVideoIcon />
}
function ManageAudioBook() {
    return <ClearAllIcon />
}