import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Link from '@mui/material/Link';
import Typography from '@mui/material/Typography';

import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { useState, SyntheticEvent } from 'react';

export default function FAQ(props: {
  title?: string,
  content: { q: string | any, a: string | any, id: string }[]
}) {
  const [expanded, setExpanded] = useState<string | false>(false);


  const handleChange =
    (panel: string) => (event: SyntheticEvent, isExpanded: boolean) => {
      setExpanded(isExpanded ? panel : false);
    };

  return (
    <Container
      id="faq"
      sx={{
        pt: { xs: 4, sm: 12 },
        pb: { xs: 8, sm: 16 },
        position: 'relative',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        gap: { xs: 3, sm: 6 },
      }}
    >
      <Typography
        component="h2"
        variant="h4"
        color="text.primary"
        sx={{
          width: { sm: '100%', md: '60%' },
          textAlign: { sm: 'left', md: 'center' },
        }}
      >
        {props.title}
      </Typography>
      <Box sx={{ width: '100%' }}>
        {props.content.map(content => <Accordion key={content.id}
          expanded={expanded === content.id}
          onChange={handleChange(content.id)}
        >
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls={content.id + "d-content"}
            id={content.id + "-header"}
          >
            <Typography component="h3" variant="subtitle2">
              {content.q}
            </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography
              variant="body2"
              gutterBottom
              sx={{ maxWidth: { sm: '100%', md: '70%' } }}
            >
              {content.a}
            </Typography>
          </AccordionDetails>
        </Accordion>)}
      </Box>
    </Container>
  );
}
