import { AppBar, Box, Button, Container, Divider, Drawer, IconButton, MenuItem, PaletteMode, Toolbar } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import CancelIcon from '@mui/icons-material/Cancel';
import Sitemark from './SitemarkIcon';
import { Link } from 'react-router-dom';
import { useContext, useState } from 'react';
import { DisplayUser } from '../DisplayUser';

export default function AppAppBar(props: {
  links?: { name: string, to: string }[],
  nologin?: boolean
}) {
  const [open, setOpen] = useState(false);
  const user = undefined as any

  const toggleDrawer = (newOpen: boolean) => () => {
    setOpen(newOpen);
  };

  const scrollToSection = (sectionId: string) => {
    const sectionElement = document.getElementById(sectionId);
    const offset = 128;
    if (sectionElement) {
      const targetScroll = sectionElement.offsetTop - offset;
      sectionElement.scrollIntoView({ behavior: 'smooth' });
      window.scrollTo({
        top: targetScroll,
        behavior: 'smooth',
      });
      setOpen(false);
    }
  };

  return (
    <AppBar
      position="fixed"
      sx={{
        boxShadow: 0,
        bgcolor: 'transparent',
        backgroundImage: 'none',
        mt: 2,
      }}
    >
      <Container maxWidth="lg">
        <Toolbar
          variant="regular"
          sx={(theme) => ({
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
            flexShrink: 0,
            borderRadius: '999px',
            bgcolor:
              theme.palette.mode === 'light'
                ? 'hsla(220, 60%, 99%, 0.6)'
                : 'hsla(220, 0%, 0%, 0.7)',
            backdropFilter: 'blur(24px)',
            maxHeight: 40,
            border: '1px solid',
            borderColor: 'divider',
            boxShadow:
              theme.palette.mode === 'light'
                ? '0 1px 2px hsla(210, 0%, 0%, 0.05), 0 2px 12px hsla(210, 100%, 80%, 0.5)'
                : '0 1px 2px hsla(210, 0%, 0%, 0.5), 0 2px 12px hsla(210, 100%, 25%, 0.3)',
          })}
        >
          <Box
            sx={{
              flexGrow: 1,
              display: 'flex',
              alignItems: 'center',
              px: 0,
            }}
          >
            <Sitemark />
            <Box sx={{ display: { xs: 'none', md: 'flex' } }}>
              {props.links?.map(l => <Link to={l.to} key={l.to} className='hover:underline text-gray-700 px-2'>{l.name}</Link>)}
            </Box>
          </Box>
          <Box
            sx={{
              display: { xs: 'none', md: 'flex' },
              gap: 0.5,
              alignItems: 'center',
            }}
          >

            {!props.nologin && <>{user && user.uid ?
              <div className='text-gray-700'>
                <DisplayUser user={user} /> </div> :
              <>
                <Button color="primary" variant="text" size="small">
                  Sign in
                </Button>
                <Button color="primary" variant="contained" size="small">
                  Sign up
                </Button></>}</>}
          </Box>
          <Box sx={{ display: { sm: 'flex', md: 'none' } }}>
            <IconButton aria-label="Menu button" onClick={toggleDrawer(true)}>
              <MenuIcon />
            </IconButton>
            <Drawer anchor="top" open={open} onClose={toggleDrawer(false)}>
              <Box
                sx={{
                  p: 2,
                  backgroundColor: 'background.default',
                }}
              >
                <Box
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'space-between',
                  }}
                >
                  <IconButton onClick={toggleDrawer(false)}>
                    <CancelIcon />
                  </IconButton>
                </Box>
                <Divider sx={{ my: 3 }} />
                {props.links?.map(l => <Link to={l.to} key={l.to} className='hover:underline text-gray-700'>
                  <MenuItem onClick={() => scrollToSection('features')}>
                    {l.name}
                  </MenuItem></Link>)}
                {!props.nologin && <>{user && user.uid ?
                  <div className='text-gray-700'>
                    <DisplayUser user={user} /> </div> :
                  <>
                    <MenuItem>
                      <Button color="primary" variant="contained" fullWidth>
                        Sign up
                      </Button>
                    </MenuItem>
                    <MenuItem>
                      <Button color="primary" variant="outlined" fullWidth>
                        Sign in
                      </Button>
                    </MenuItem></>}</>}

              </Box>
            </Drawer>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
