import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import IconButton from '@mui/material/IconButton';
import InputLabel from '@mui/material/InputLabel';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';

import { visuallyHidden } from '@mui/utils';

import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import TwitterIcon from '@mui/icons-material/X';

import SitemarkIcon from './SitemarkIcon';
import { Link } from 'react-router-dom';

function Copyright(props: { link: string, name: string }) {
  return (
    <Typography variant="body2" color="text.secondary" mt={1}>
      {'Copyright © '}
      <a href={props.link}>{props.name}&nbsp;</a>
      {new Date().getFullYear()}
    </Typography>
  );
}

export default function Footer(props: {
  company: string,
  companylink: string,
  ppLink?: string,
  tosLink?: string,
  gh?: string,
  li?: string,
  x?: string,
  sections?: {
    title: string,
    items: {
      title: string,
      link: string
    }[]
  }[],
  subcontent?: JSX.Element

}) {
  return (
    <Container
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        gap: { xs: 4, sm: 8 },
        py: { xs: 8, sm: 10 },
        textAlign: { sm: 'center', md: 'left' },
      }}
    >
      {props.sections && <Box
        sx={{
          display: 'flex',
          flexDirection: { xs: 'column', sm: 'row' },
          width: '100%',
          justifyContent: 'space-between',
        }}
      >
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'column',
            gap: 4,
            minWidth: { xs: '100%', sm: '60%' },
          }}
        >
          <Box sx={{ width: { xs: '100%', sm: '60%' } }}>
            {props.subcontent}
          </Box>
        </Box>
        {props.sections?.map(section => <Box
          sx={{
            display: { xs: 'none', sm: 'flex' },
            flexDirection: 'column',
            gap: 1,
          }}
        >
          <Typography variant="body2" fontWeight="medium">
            {section.title}
          </Typography>
          {section.items.map(item =>
            <Link className='hover:underline' to={item.link}>
              {item.title}
            </Link>
          )}
        </Box>)}
      </Box>}
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'space-between',
          pt: { xs: 4, sm: 8 },
          width: '100%',
          borderTop: '1px solid',
          borderColor: 'divider',
        }}
      >
        <div>
          {props.ppLink && <Link color="text.secondary" to={props.ppLink}>
            Privacy Policy
          </Link>}
          {props.ppLink && props.tosLink && <Typography display="inline" sx={{ mx: 0.5, opacity: 0.5 }}>
            &nbsp;•&nbsp;
          </Typography>}
          {props.tosLink && <Link color="text.secondary" to={props.tosLink}>
            Terms of Service
          </Link>}
          <Copyright name={props.company} link={props.companylink} />
        </div>
        <Stack
          direction="row"
          justifyContent="left"
          spacing={1}
          useFlexGap
          sx={{
            color: 'text.secondary',
          }}
        >
          {props.gh && <IconButton
            color="inherit"
            href={props.gh}
            aria-label="GitHub"
            sx={{ alignSelf: 'center' }}
          >
            <GitHubIcon />
          </IconButton>}
          {props.x && <IconButton
            color="inherit"
            href={props.x}
            aria-label="X"
            sx={{ alignSelf: 'center' }}
          >
            <TwitterIcon />
          </IconButton>}
          {props.li && <IconButton
            color="inherit"
            href={props.li}
            aria-label="LinkedIn"
            sx={{ alignSelf: 'center' }}
          >
            <LinkedInIcon />
          </IconButton>}
        </Stack>
      </Box>
    </Container>
  );
}
